import { Injectable } from '@angular/core';
import { JwtHelper, tokenNotExpired } from 'angular2-jwt';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/map';

import { AppError } from '../common/app-error';
import { NotFoundError } from '../common/not-found-error';
import { BadRequestError } from './../common/bad-request-error';
import { UnauthorizedError } from '../common/unauthorized-error';
import { HttpClient } from '@angular/common/http';
import { NoConnectionError } from '../common/no-connection-error';
import { RequestOptions, Headers, Http } from '@angular/http';

@Injectable()
export class DataService {

  response: any;
  headers = new Headers();
  token = localStorage.getItem('token');
  options: RequestOptions;

  constructor(private url: string, private http: Http) {
    this.headers.append('Authorization', 'Bearer ' + this.token);
    this.options = new RequestOptions({ headers: this.headers });
   }

  all(filter) {
    return this.http.get(this.url + '?' + this.toQueryString(filter))
      .catch(this.handleError);
  } 

  get(pageIndex, pageSize) {
    return this.http.get(this.url + '/' + pageIndex + '/' + pageSize, this.options)
      .catch(this.handleError);
  }

  getAll() {
    return this.http.get(this.url, this.options)
      .catch(this.handleError);
  }

  getByUrl(url) {
    return this.http.get(url, this.options)
      .catch(this.handleError);
  }

  getById(id) {
    return this.http.get(this.url + '/' + id, this.options)
      .catch(this.handleError);
  }

  create(resource) {
    return this.http.post(this.url, resource, this.options)
      .catch(this.handleError);
  }

  createByUrl(url, resource) {
    return this.http.post(url, resource, this.options)
      .catch(this.handleError);
  }

  update(resource) {
    return  this.http.put(this.url + '/' + resource.id, resource, this.options)
      .catch(this.handleError);
  }

  delete(id) {
    return this.http.delete(this.url + '/' + id, this.options)
      .catch(this.handleError);
  }

// if error occur
// changes were made here,
// != replaced !==
  toQueryString(obj) {
    const parts = [];

    for ( const property in obj) {
      const value = obj[property];

      if (value !== null && value !== undefined)
        parts.push(encodeURIComponent(property) + '=' + encodeURIComponent(value));
    } 
    return parts.join('&');
  }


  private handleError(error: Response) {
    if (error.status === 400) {
      return Observable.throw(new BadRequestError(error));
    }
    if (error.status === 404) {
      return Observable.throw(new NotFoundError(error));
    }
    if (error.status === 401) {
      return Observable.throw(new UnauthorizedError(error));
    }
    if (error.status === 0) {
      return Observable.throw(new NoConnectionError(error));
    }
    return Observable.throw(new AppError(error));

  }
}
