import { Injectable } from '@angular/core';
import { DataService } from '../../services/data.service';
import { Http } from '@angular/http';

@Injectable()
export class EmployeeService extends DataService {
  urlForRegistration: string;
  urlForGetbyEmployeeNumber: string;
  constructor(http: Http) {
    super('http://jonelbucad21-001-site1.ftempurl.com/api/employees', http);
    this.urlForRegistration = 'http://jonelbucad21-001-site1.ftempurl.com/api/employees/forregistration';
    this.urlForGetbyEmployeeNumber = 'http://jonelbucad21-001-site1.ftempurl.com/api/employees/?employeeNumber=';
  }
  getByEmployeeNumber(employeeNumber: string) {
    return this.urlForGetbyEmployeeNumber = this.urlForGetbyEmployeeNumber + employeeNumber;
  }

}
